    position: relative;
    cursor: pointer;
}

.tech{
    background-color: #2C3E50;
    width: 100%;
    height: 20%;
    position: absolute;
    bottom: 0px;
    border-bottom-right-radius: 8px;
    border-bottom-left-radius: 8px;
}
.tech p{
    position: absolute;
    font-size: 15px;
