}
.image {
    opacity: 1;
    display: block;
    width: 100%;
    height: 100%;
    transition: .5s ease;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
  }

.opacity {
    transition: .5s ease;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
  }
  .info {
  
    color: black;
    font: bold;
    font-size: 20px;
  }

  .item:hover .image {
    opacity: 0.3;
  }
  
  .item:hover .opacity {
    opacity: 1;
  }
  

