}

.navbar  .active{
  color: #00d8ff !important;
  opacity: 1 !important;
  pointer-events: none;
  border-top: 1px solid #00d8ff;
  border-bottom: 1px solid #00d8ff;
}


.navbar .inactive{
    color: white;
    opacity: 0.6;
    transition: 0.3s;
   /* border-bottom: 1px solid;
