@import url('https://fonts.googleapis.com/css?family=Lexend+Deca&display=swap');

.global-font {
  font-family: 'Lexend Deca', sans-serif;
}
.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  flex-shrink: 0;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}



