.flex-container{
  display: flex;
  align-items: stretch;
  height: 100vh;
  min-height: 100%;
  flex-wrap: wrap;
}

.column{
  flex-shrink: 1;
  flex-grow: 0;
  flex-basis: 14.28%;
  height: 70%;
}

.column2{
  flex-shrink: 1;
  flex-grow: 0;
  flex-basis: 14.28%;
  height: 10%;
}

.column3, .column3-last{
  flex-shrink: 1;
  flex-grow: 0;
  flex-basis: 16.5%;
  height: 20%;
}

.column3-last{
  flex-grow: 1;
}


